const timeDisplay = (seconds: number) => {
  const mDisp: string = Math.floor(seconds / 60).toString();
  let sDisp: string = Math.floor(seconds % 60).toString();
  if (sDisp.length == 1) {
    sDisp = "0" + sDisp;
  }
  return mDisp + ":" + sDisp;
};

const Playlist = (Props) => {
  let audioContainer: any = [];
  Props.audioFiles.map((a: any, i: number) => {
    audioContainer.push(useAudio({ src: a.publicURL }));
  });

  return (
    <div className={Styles.playlist}>
      <table>
        <thead>
          <tr>
            <th>Name</th>
            <th>Size</th>
            <th>Length</th>
          </tr>
        </thead>
        <tbody>
          {Props.audioFiles.map((f, i: number) => {
            const [audio, state, controls, ref] = audioContainer[i];
            const durationDisplay = timeDisplay(state["duration"]);

            return (
              <tr
                key={`track-${i}`}
                onClick={() => playPause(audio, state, controls)}
              >
                {audio}
                <td>{f.relativePath}</td>
                <td>{f.prettySize}</td>
                <td>{durationDisplay}</td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
};

export default Playlist;
